import { mapGetters, mapActions } from 'vuex'
import _ from 'lodash'

export default {
  data () {
    return {
      tabPosition: 'left',
      labelPosition: 'top', // 弹框label位置
      tabName: 'first',
      multipleSelection: [], // 存储复选框
      dialogVisible: false, // 弹框 添加ip
      getewayDialogVisible: false, // 10 添加网关子网弹框
      title: '新增IP',
      getewayTitle: '新增网关子网',
      form: { TenantName: '' }, // 租户下拉框 租户名
      deleteForm: { // 6 存储要删除的数据
        StaticIps: [],
        TenantName: ''
      },
      staticIps: [], // 6 临时存储删除时checked的ip名
      addIpsForm: { // 4 新增ip
        StartIp: '',
        EndIp: '',
        TenantName: ''
      },
      keywords: '', // 7搜索关键字
      getewayForm: { // 10 添加网关子网form
        Subnet: '',
        Gateway: ''
      },

      total: 0, // 9 总的数量长度
      currentPage: 1, // 当秋安页
      pagesize: 10, // 每页显示的数量
      InfoList: [], // ，所有要显示的数据
      everyInfoList: [], // ，每页显示的数据

      total1: 0, // 9 总的数量长度
      currentPage1: 1, // 当秋安页
      InfoList1: [], // ，所有要显示的数据
      everyInfoList1: [], // ，每页显示的数据

      // 新增ip弹框的表单验证// 8 表单验证
      rules: {
        StartIp: [
          { required: true, message: '不能为空', trigger: 'blur' },
          { pattern: /^((25[0-5]|2[0-4]\d|((1\d{2})|([1-9]?\d)))\.){3}(25[0-5]|2[0-4]\d|((1\d{2})|([1-9]?\d)))$/,
            message: '请输入有效的IP地址',
            trigger: 'blur' }
        ],
        EndIp: [
          { required: true, message: '不能为空', trigger: 'blur' },
          { pattern: /^((25[0-5]|2[0-4]\d|((1\d{2})|([1-9]?\d)))\.){3}(25[0-5]|2[0-4]\d|((1\d{2})|([1-9]?\d)))$/,
            message: '请输入有效的IP地址',
            trigger: 'blur'
          }
        ],
        TenantName: [
          { required: true, message: '请选择租户', trigger: 'change' }
        ],
        Gateway: [
          { required: true, message: '网关不能为空', trigger: 'blur' },
          { pattern: /^((25[0-5]|2[0-4]\d|((1\d{2})|([1-9]?\d)))\.){3}(25[0-5]|2[0-4]\d|((1\d{2})|([1-9]?\d)))$/,
            message: '请输入有效的网关地址',
            trigger: 'blur'
          }
        ],
        Subnet: [
          { required: true, message: '子网不能为空', trigger: 'blur' },
          {
            // pattern: /^((25[0-5]|2[0-4]\d|((1\d{2})|([1-9]?\d)))\.){3}(25[0-5]|2[0-4]\d|((1\d{2})|([1-9]?\d)))\/(25[0-5]|2[0-4]\d|((1\d{2})|([1-9]?\d)))$/,
            message: '请输入有效的子网地址',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created: function () {
    // 1 ip地址池
    this.getAll().then((res) => {
      var arr = []
      _.forIn(res.data, function (value, key) {
        arr.push(value)
      })
      this.InfoList = _.clone(arr)
      this.total = this.InfoList.length // 6 总数
      //  页数 如有小数部分 只取整数部分
      // var pageNum = Number(String(this.total / this.pagesize).split('.')[0])
      var newArr = [] // 根据每页显示数量计算共分为几组
      this.InfoList.forEach((index) => {
        newArr.push(this.InfoList.splice(0, this.pagesize))
      })
      //  如果剩余的数据小于每一页的数量 把剩余的放入newArr数组中
      if (this.InfoList.length < this.pagesize) {
        newArr.push(this.InfoList.splice(0, this.InfoList.length))
      }
      this.InfoList = newArr
      this.everyInfoList = this.InfoList[0]
    }).catch((error) => {
      console.log(error)
    })
    this.getCurrentUserInfo() // 获取当前登录的用户
    this.getTenants() // 4 获取所有租户列表
    this.getGateway() // 网关子网
  },

  mounted: function () {
    this.addInterceptors()
  },

  watched: function () {
    this.getCurrentUserInfo()
    this.getTenants()
  },

  computed: {
    //  6 把复选框的checked的ips 取值给 staticIps 再赋值给deleteForm
    deleteForm2: function () {
      this.multipleSelection.forEach((item, index) => {
        // 把复选框checked的放到staticIps数组中
        this.staticIps.push(item.ipName)
      })
      this.deleteForm.StaticIps = this.staticIps
      this.deleteForm.TenantName = this.form.TenantName
      this.staticIps = []
      // console.log(this.deleteForm)
      // 赋值后清除数据再次选择时数据不会重复
    },

    // 7 搜索框 (通过ip地址 租户 应用 服务 pod搜索)
    ipsList: function () {
      var vm = this
      return this.everyInfoList.filter(function (item) {
        var res = (item.StaticIp.indexOf(vm.keywords) !== -1) ||
        (item.TenantName.indexOf(vm.keywords) !== -1) ||
        (item.AppName.indexOf(vm.keywords) !== -1) ||
        (item.ServiceName.indexOf(vm.keywords) !== -1) ||
        (item.PodName.indexOf(vm.keywords) !== -1)
        return res
      })
    },

    // 获取绑定的数据变量
    ...mapGetters(['ips', 'unUseIps', 'currentUserInfo',
      'tenants', 'VerityForm', 'CheckedRules', 'gateway'])
  },

  methods: {
    computerInfo () {
      // this.getAll().then(() => {
      this.computeIpsList()
      // })
    },
    // 10 网关子网 删除
    DeleteGateway (index, row) {
      var obj = { }
      obj.Subnet = row.Subnet
      this.$confirm('此操作将会删除当前数据,是否继续？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.singleDeleteSubnet(obj).then((res) => {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
        }).catch((error) => {
          console.log(error)
          // const message =
          if (error.response.status) {
            this.$message({
              showClose: true,
              message: error.response.data.message,
              type: 'error'
            })
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除',
          showClose: true
        })
      })
    },
    // 10 提交弹框表单 添加网关
    submitGetewayForm (data) {
      this.$refs.getewayForm.validate((valid) => {
        if (valid) {
          this.singleAddGeteway(data).then((res) => {
            this.$message({
              showClose: true,
              message: '添加成功',
              type: 'success'
            })
            this.getewayDialogVisible = false
            this.$refs.getewayForm.resetFields()
          }).catch((error) => {
            this.$message({
              showClose: true,
              message: error.response.data.message,
              type: 'error'
            })
            console.log(error)
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 10 添加网关 弹框弹出
    addGeteway (form) {
      this.getewayDialogVisible = true
    },
    //  10  关闭添加网关弹框
    closeGetewayDialog () {
      this.getewayDialogVisible = false
      //  清空表单内容
      this.$refs.getewayForm.resetFields()
    },

    // 9 未分配分页
    computeUnUseIPs () {
      this.InfoList1 = _.clone(this.unUseIps)
      this.total1 = this.InfoList1.length
      // var pageNum1 = Number(String(this.total1 / this.pagesize).split('.')[0])
      var newArr1 = []
      this.InfoList1.forEach((index) => {
        newArr1.push(this.InfoList1.splice(0, this.pagesize))
      })
      if (this.InfoList1.length < this.pagesize) {
        newArr1.push(this.InfoList1.splice(0, this.InfoList1.length))
      }
      this.InfoList1 = newArr1
      this.everyInfoList1 = this.InfoList1[0]
    },
    handleCurrentChange1 (currentPage1) {
      this.everyInfoList1 = this.InfoList1[currentPage1 - 1]
    },
    handleCurrentChange (currentPage) {
      this.everyInfoList = this.InfoList[currentPage - 1]
    },

    //  6 删除ip
    deleteStaticIPs (data) {
      console.log(data)
      console.log(data.TenantName)
      this.$confirm('此操作将会删除当前数据,是否继续？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.batchDeleteStaticIps(data).then((res) => {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
          // 删除后再进行根据该租户名查询进行列表更新 否则删除后列表不更新
          this.submitForm(data.TenantName)
        }).catch((error) => {
          console.log(error)
          this.$message({
            showClose: true,
            message: error.response.data.message,
            type: 'error'
          })
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除',
          showClose: true
        })
      })
    },

    // 4.1 取消弹框操作
    callOff () {
      this.dialogVisible = false
      // location.reload()
      // 清除表单内容
      this.$refs.addIpsForm.resetFields()
    },
    //  4新增IP
    addIP () {
      this.dialogVisible = true
    },
    addIPSubmitForm (data) {
      console.log(data)
      this.$refs.addIpsForm.validate((valid) => {
        if (valid) {
          this.addStaticIP(data).then((res) => {
            // 为某租户添加ips后 下拉框显示新添加ips的租户
            this.form.TenantName = data.TenantName
            this.$message({
              showClose: true,
              message: '添加IP成功',
              type: 'success'
            })
            // 添加ip后 根据租户更新未使用的ips
            this.submitForm(data.TenantName)
            this.dialogVisible = false
          }).catch((error) => {
            console.log(error)
            this.$message({
              showClose: true,
              message: error.response.data.message,
              type: 'error'
            })
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    //  2 根据租户查询未使用ip
    submitForm (data) {
      this.getUnUseIp(data)
    },
    // 2根据租户查询未使用ip获取未分配的ip
    getUnUseIp (data) {
      this.getUnUseIps(data).then((result) => {
        // 根据租户获取相关ip后执行分页操作
        this.computeUnUseIPs()
      }).catch((error) => {
        console.log(error)
        this.everyInfoList1 = []
      })
    },

    // 3复选框勾选 单选/全选/多选
    handleSelectionChange (val) {
      this.multipleSelection = val
    },

    // 1 获取ips UnUseIps的函数
    ...mapActions(['getAll', 'getUnUseIps', 'getCurrentUserInfo',
      'getTenants', 'batchDeleteStaticIps', 'addStaticIP', 'getGateway',
      'singleDeleteSubnet', 'singleAddGeteway', 'addInterceptors'])
  }
}



