import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld
import IpList from '@/pages/ip-list/ip-list.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'IpList',
      component: IpList
    }
  ]
})



