import axios from 'axios'
import _ from 'lodash'

// 获取当前登录的token
var token = window.localStorage.getItem('DCE_TOKEN')
axios.defaults.headers['Content-Type'] = 'application/json'
axios.defaults.headers['X-DCE-Access-Token'] = token

export default {
  state: {
    unUseIps: [], // 2 未分配ip
    currentUserInfo: [], // 3 当前登录的用户
    tenants: [], // 4 所有租户列表
    gateway: [] // 网关子网
  },
  getters: {
    unUseIps: state => state.unUseIps,
    currentUserInfo: state => state.currentUserInfo,
    tenants: state => state.tenants,
    gateway: state => state.gateway

  },
  mutations: {

    // 2 获取未分配ip
    alterUnUseIps: (state, data) => {
      state.unUseIps = data
      // console.log('未分配', state.unUseIps)
    },

    // 3 获取当前登录的用户
    alterCurrentUserInfo: (state, data) => {
      state.currentUserInfo = data
      // console.log('当前登录的用户：', state.currentUserInfo)
    },

    // 4 获取租户列表
    alterTenants: (state, data) => {
      state.tenants = data
      // console.log('租户列表', state.tenants)
    },
    // 4 获取租户列表
    alterGateway: (state, data) => {
      state.gateway = data
      // console.log('网关子网', state.gateway)
    }
  },
  actions: {
    // axios拦截请求
    addInterceptors () {
      // 11  发送请求
      axios.interceptors.request.use((config) => {
        // console.log(`请求地址: ${config.url}`)
        return config
      }, (error) => {
        console.log('请求失败')
        return Promise.reject(error)
      })
      // 11 响应请求
      axios.interceptors.response.use((config) => {
        return config
      }, (error) => {
        if (error.response) {
          switch (error.response.status === 400 && error.response.data.id) {
            case 'static_ip_already_exist_error':
              error.response.data.message = '创建失败，静态 IP 已存在!'
              break
            case 'static_ip_not_exist_error':
              error.response.data.message = '不存在的信息，请重试!'
              break
            case 'not_authorized_error':
              error.response.data.message = '需要管理员权限!'
              break
            case 'static_ip_format_error':
              error.response.data.message = 'IP 格式不正确!'
              break
            case 'static_ip_range_too_big':
              error.response.data.message = 'IP 范围过大!'
              break
            case 'sip_ip_range_err':
              error.response.data.message = 'IP范围错误!'
              break
            case 'static_ip_not_belong_to_tenant':
              error.response.data.message = '静态 IP 不属于该租户!'
              break
            case 'sip_ip_gateway_format_error':
              error.response.data.message = 'sorry，该网关不在子网段内!'
              break
            default:
              error.response.data.message = '未知错误'
          }
        } else {
          error.response.data.message = '连接服务器失败！'
        }
        return Promise.reject(error)
      })
    },
    // 10 获取子网网关
    getGateway: (context) => {
      axios.get('./api/v1/gateway').then((res) => {
        // console.log(res.data)
        context.commit('alterGateway', res.data)
      })
    },
    //  10 网关子网 删除子网
    singleDeleteSubnet: (context, form) => {
      // console.log(form)
      form = JSON.stringify(form)
      console.log(form)
      return new Promise((resolve, reject) => {
        axios.delete('./api/v1/gateway', {data: form})
          .then(function (result) {
            // alert
            context.dispatch('getGateway')
            resolve(result)
          }).catch((error) => {
            reject(error)
            console.log(error)
          })
      })
    },
    // 10 新增网关子网
    singleAddGeteway: (context, form) => {
      form = JSON.stringify(form)
      return new Promise((resolve, reject) => {
        axios.post('./api/v1/gateway', form)
          .then(function (result) {
            // 添加成功后 根据form的TenantName更新该租户的未使用的ips
            context.dispatch('getGateway')
            resolve(result)
          }).catch((error) => {
            reject(error)
            console.log(error)
            console.log(error.response.data.id)
          })
      })
    },

    // 1 获取ip地址池 已分配
    getAll: (context) => {
      return new Promise((resolve, reject) => {
        axios.get('./api/v1/static_ip').then((res) => {
          console.log('ip地址池：', res.data)
          resolve(res)
        }).catch(error => {
          reject(error)
        })
      })
    },

    // 2 未分配ip
    getUnUseIps: (context, data) => {
      return new Promise((resolve, reject) => {
        axios.get(`./api/v1/tenant_ip?TenantName=${data}&All=0&Unuse=1`)
          .then(function (result) {
            var arr = []
            _.mapKeys(result.data, function (value, key) {
              arr.push(value)
              var obj = {}
              var arr2 = []
              for (var i in arr[0]) {
                obj.ipName = arr[0][i]
                arr2.push(obj)
                obj = {}
              }
              context.commit('alterUnUseIps', arr2)
              resolve(arr2)
            })
          }).catch((error) => {
            reject(error)
            console.log(error)
            var arr = []
            context.commit('alterUnUseIps', arr)
          })
      })
    },

    // 3 获取当前登录用户
    getCurrentUserInfo: (context) => {
      axios.get('/dce/my-account').then((res) => {
        // console.log('当前登录的用户', res.data)
        context.commit('alterCurrentUserInfo', res.data)
      })
    },

    // 4 获取租户列表
    getTenants: (context) => {
      axios.get('./api/v1/get_tenant_name').then((res) => {
        context.commit('alterTenants', res.data)
      })
    },

    // 5 删除ip
    batchDeleteStaticIps: (context, form) => {
      return new Promise((resolve, reject) => {
        axios.post('./api/v1/bulk_delete_sip', form)
          .then(function (result) {
            context.dispatch('getUnUseIps', form.TenantName)
            resolve(result)
          }).catch((error) => {
            reject(error)
          })
      })
    },

    // 6 新增IP
    addStaticIP: (context, form) => {
      console.log('添加的租户', form)
      return new Promise((resolve, reject) => {
        axios.post('./api/v1/tenant_ip', form)
          .then(function (result) {
            // 添加成功后 根据form的TenantName更新该租户的未使用的ips
            context.dispatch('getUnUseIps', form.TenantName)
            resolve(result)
          }).catch((error) => {
            reject(error)
          })
      })
    }
  }
}



